body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
    font-family: 'digital';
    font-style: normal;
    font-weight: 400;
    src:url(/react-courses/static/media/digital_counter_7.52a6484f.ttf) format('truetype');
}
.calculator {
    width: 200px;
    height: 300px;
    border-radius: 10px;
    border: 2px solid black;
    margin: 40px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.screen{
    font-family: 'digital';
    text-align: right;
    width: 91%;
    -webkit-transform: scaleY(2);
            transform: scaleY(2);
    font-size: 25px;
}

.buttons {
    width: 93%;
}

button {
    height: 40px;
    width: 40px;
    margin: 3px;
}
